@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root, .App {
  height: 100%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  background: #e6dee9;
  font-family: 'Roboto Slab', sans-serif;
  color: #2f2d2d;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

.App {
  display: flex;
  flex-direction: column;
  align-items: center; }

.homeScreen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: 100%;
  padding: 52px;
  font-weight: 700; }
  .homeScreen .startBtn {
    text-transform: uppercase;
    border-radius: 8px;
    height: 21vw;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 -3px 0 0 #2545a8 inset;
    background-color: #6683dd;
    color: #f8f6f9;
    font-size: 32px;
    padding: 16px; }
  .homeScreen .menuItem {
    background-color: #575353;
    box-shadow: 0 -3px 0 0 #393636 inset;
    color: #f8f6f9;
    padding: 16px;
    border-radius: 8px;
    box-sizing: inherit;
    line-height: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    margin-top: 12px;
    text-transform: uppercase; }
  .homeScreen .modeSelector {
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
    grid-gap: 12px;
    width: 100%;
    font-size: 28px; }

.statsScreen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: calc(84vw + 24px); }

.counter {
  font-weight: 700;
  margin: 20px 0;
  width: calc(84vw + 24px);
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .counter .title {
    font-size: 24px; }
  .counter .cancelBtn {
    background-color: #575353;
    box-shadow: 0 -2px 0 0 #393636 inset;
    color: #f8f6f9;
    height: 20px;
    padding: 6px 12px;
    font-size: 15px;
    border-radius: 8px;
    border: none;
    display: block;
    font: inherit;
    line-height: 1;
    box-sizing: content-box; }

.board {
  display: grid;
  grid-gap: 12px;
  grid-template-columns: 28vw 28vw 28vw; }

.cell {
  height: 28vw;
  border-radius: 16px;
  background-color: #6D435A;
  color: #f8f6f9;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 72px;
  text-shadow: 3px 4px 0px rgba(0, 0, 0, 0.3);
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }
  .cell .displayLetter.show {
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear; }
    .cell .displayLetter.show.show {
      -webkit-transition: all 0.3s ease;
      transition: all 0.3s ease; }

.btnRow {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 12px;
  width: calc(84vw + 24px);
  margin: 24px 0; }
  .btnRow .matchBtn {
    border-radius: 8px;
    height: 14vw;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    font-weight: 700;
    background-color: #f8f6f9;
    box-shadow: 0 -3px 0 0 #ccc9c9 inset; }
  .btnRow .matchBtn.active {
    background-color: #6683dd;
    box-shadow: 0 -2px 0 0 #2545a8 inset;
    color: #f8f6f9; }

.progressBarHolder {
  position: relative;
  width: calc(84vw + 24px);
  height: 8px;
  border-radius: 4px;
  background-color: #ccc9c9;
  box-shadow: 0 -1px 0 0 #b9b5b5 inset; }
  .progressBarHolder .progress {
    border-radius: 4px;
    position: absolute;
    background-color: #2545a8;
    height: 100%; }
  .progressBarHolder .progress.loading {
    -webkit-transition: width 0.3s linear;
    transition: width 0.3s linear;
    box-shadow: 0 -1px 0 0 #1e3888 inset; }

.pager {
  margin: 24px 0;
  font-size: 24px;
  text-align: center; }

